  margin-bottom: 13px;
  position: relative;
  width: 100%;
  text-align: left;
}
.accordeon__check {
  display: none;
}
.accordeon__check:checked ~ .accordeon__content {
  height: 140px;
}
.accordeon__check:checked ~ .accordeon__label {
  background-color: #ec4848;
}
.accordeon__check:checked ~ .accordeon__label .accordeon__plus {
  opacity: 0;
}
.accordeon__check:checked ~ .accordeon__label .accordeon__minus {
  opacity: 1;
}
.accordeon__label {
  position: relative;
  padding-left: 15px;
  display: block;
  height: 57px;
  line-height: 57px;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  color: #fff;
  font-weight: 600;
  background-color: #3f444a;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.35s ease;
}
.accordeon__label:hover {
  background-color: #596068;
}
.accordeon__label::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  right: 60px;
  height: 100%;
  width: 1px;
  background-color: #bebebe;
}
.accordeon__content {
  margin-top: -1px;
  height: 0;
  background-color: #f8f8f8;
  border: 1px solid #c4c4c4;
  border-radius: 2px;
  overflow: hidden;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.accordeon__text {
  margin-top: 35px;
  margin-right: 25px;
  margin-left: 25px;
  font-size: 14px;
  color: #3f444a;
}
.accordeon__plus {
  position: absolute;
  right: 18px;
  top: 50%;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  font-size: 22px;
  background-color: #afafaf;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #696e72;
  transform: translateY(-50%);
  transition: all 0.35s ease;
}
.accordeon__plus::after {
  content: '';
  position: absolute;
  top: 50%;
  right: -18px;
  display: block;
  width: 20px;
  height: 8px;
  transform: translateY(-50%);
  background-color: #afafaf;
}
.accordeon__minus {
  position: absolute;
  right: 18px;
  top: 50%;
  width: 25px;
  height: 25px;
  line-height: 21px;
  text-align: center;
  font-size: 25px;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #fff;
  transform: translateY(-50%);
  opacity: 0;
  transition: all 0.35s ease;
}
.accordeon__minus::after {
  content: '';
  position: absolute;
  top: 50%;
  right: -18px;
  display: block;
  width: 18px;
  height: 8px;
  transform: translateY(-50%);
  background-color: #fff;
}
